import {
  HTMLAttributes,
  useCallback,
  useEffect,
  useState,
} from 'preact/compat';
import { Container, SlotProps } from '@adobe-commerce/elsie/lib';
import { Wishlist as WishlistComponent } from '@/wishlist/components';
import { events, WishlistActionPayload } from '@adobe-commerce/event-bus';
import {
  Item,
  Product,
  Wishlist as WishlistModel,
} from '@/wishlist/data/models';
import { state } from '@/wishlist/lib/state';
import { WishlistAlert } from '@/wishlist/containers/WishlistAlert';
import { ImageProps } from '@adobe-commerce/elsie/components';

export interface WishlistProps extends HTMLAttributes<HTMLDivElement> {
  routeEmptyWishlistCTA?: () => string;
  routeToWishlist?: string;
  moveProdToCart: (
    products: { sku: string; quantity: number }[]
  ) => Promise<any>;
  routeProdDetailPage: (product: Product) => string;
  getProductData?: (sku: string) => Promise<Product | null>;
  getRefinedProduct?: (
    sku: string,
    optionUIDs: string[],
    anchorOptions?: string[],
    raw?: boolean
  ) => Promise<Product | null>;
  slots?: {
    image?: SlotProps<{
      defaultImageProps: ImageProps;
      item: Item;
    }>;
  };
}

export const Wishlist: Container<WishlistProps> = ({
  routeEmptyWishlistCTA,
  routeToWishlist,
  moveProdToCart,
  routeProdDetailPage,
  getProductData,
  getRefinedProduct,
  slots,
  ...props
}: WishlistProps) => {
  const [wishlistData, setWishlistData] = useState<WishlistModel | null>(null);
  const [isLoggedIn, setIsLoggedIn] = useState(state.authenticated);
  const [isLoading, setIsLoading] = useState(state.isLoading);

  const handleAuthentication = (authenticated: boolean) =>
    setIsLoggedIn(authenticated);

  const [wishlistAlert, setWishlistAlert] = useState<Element | null>(null);
  const handleWishlistAlert = useCallback(
    (payload: WishlistActionPayload) => {
      const { action, item } = payload;
      setWishlistAlert(
        <WishlistAlert
          action={action}
          item={item}
          routeToWishlist={routeToWishlist}
        />
      );
    },
    [routeToWishlist]
  );

  useEffect(() => {
    const authEvent = events.on('authenticated', handleAuthentication);
    const updateEvent = events.on('wishlist/alert', (payload) =>
      handleWishlistAlert(payload)
    );
    const dataEvent = events.on(
      'wishlist/data',
      (payload) => {
        setWishlistData(payload as WishlistModel);
        setIsLoading(false);
      },
      { eager: true }
    );
    return () => {
      authEvent?.off();
      dataEvent?.off();
      updateEvent?.off();
    };
  }, [handleWishlistAlert]);

  return (
    <WishlistComponent
      {...props}
      wishlistData={wishlistData}
      wishlistAlert={wishlistAlert}
      routeEmptyWishlistCTA={routeEmptyWishlistCTA}
      moveProdToCart={moveProdToCart}
      getProductData={getProductData}
      getRefinedProduct={getRefinedProduct}
      isLoggedIn={isLoggedIn}
      isLoading={isLoading}
      routeProdDetailPage={routeProdDetailPage}
      slots={slots}
    />
  );
};
