import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe-commerce/elsie/lib';
import { useText } from '@adobe-commerce/elsie/i18n';
import { Button, Icon, IllustratedMessage } from '@adobe-commerce/elsie/components';
import { Heart } from '@adobe-commerce/elsie/icons';

import '@/wishlist/components/EmptyWishlist/EmptyWishlist.css';

export interface EmptyWishlistProps extends HTMLAttributes<HTMLDivElement> {
  ctaLinkURL?: string;
}

export const EmptyWishlist: FunctionComponent<EmptyWishlistProps> = ({
  className,
  children,
  ctaLinkURL,
  ...props
}) => {
  const labels = useText({
    emptyWishlist: 'Wishlist.EmptyWishlist.heading',
    message: 'Wishlist.EmptyWishlist.message',
    cta: 'Wishlist.EmptyWishlist.cta',
  });

  return (
    <div {...props} className={classes(['wishlist-empty-wishlist', className])}>
      <IllustratedMessage
        className={classes(['wishlist-empty-wishlist__wrapper', className])}
        data-testid="wishlist-empty-wishlist"
        heading={labels.emptyWishlist}
        icon={<Icon className="wishlist-empty-wishlist__icon" source={Heart} />}
        message={<p>{labels.message}</p>}
        action={
          ctaLinkURL ? (
            <Button
              data-testid="wishlist-empty-wishlist-button"
              size="medium"
              variant="primary"
              key="routeHome"
              type="submit"
              href={ctaLinkURL}
            >
              {labels.cta}
            </Button>
          ) : undefined
        }
      />
    </div>
  );
};
