import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes, useState, useEffect, useMemo } from 'preact/compat';
import {
  VComponent,
  classes,
  Slot,
  SlotProps,
} from '@adobe-commerce/elsie/lib';
import { WishlistSkeleton } from '@/wishlist/components/Wishlist/WishlistSkeleton';
import '@/wishlist/components/Wishlist/Wishlist.css';
import {
  Product,
  Wishlist as WishlistModel,
  Item,
} from '@/wishlist/data/models';
import { WishlistItem, WishlistAlert } from '@/wishlist/containers';
import { Fragment } from 'react';
import { EmptyWishlist, Login } from '@/wishlist/components';
import { useText } from '@adobe-commerce/elsie/i18n';
import { ImageProps } from '@adobe-commerce/elsie/components';

export interface WishlistProps extends HTMLAttributes<HTMLDivElement> {
  className?: string;
  wishlistData: WishlistModel;
  wishlistAlert: WishlistAlert | null;
  isLoggedIn: boolean;
  isLoading: boolean;
  moveProdToCart: (
    products: { sku: string; quantity: number }[]
  ) => Promise<any>;
  getProductData?: (sku: string) => Promise<Product | null>;
  getRefinedProduct?: (
    sku: string,
    optionUIDs: string[],
    anchorOptions?: string[],
    raw?: boolean
  ) => Promise<Product | null>;
  routeEmptyWishlistCTA?: () => string;
  onLoginClick?: () => void;
  routeProdDetailPage: (product: Product) => string;
  slots?: {
    image?: SlotProps<{
      defaultImageProps: ImageProps;
      item: Item;
    }>;
  };
}

export const Wishlist: FunctionComponent<WishlistProps> = ({
  className,
  wishlistData,
  wishlistAlert,
  isLoggedIn,
  isLoading,
  moveProdToCart,
  getProductData,
  getRefinedProduct,
  routeEmptyWishlistCTA,
  onLoginClick,
  routeProdDetailPage,
  slots,
  ...props
}) => {
  const [alert, setAlert] = useState<VNode | null>(wishlistAlert);

  const dictionary = useText({
    wishlistHeading: 'Wishlist.Wishlist.heading',
    wishlistLoadingHeading: 'Wishlist.Wishlist.loading',
  });

  const products = useMemo(() => {
    return wishlistData?.items?.length > 0
      ? wishlistData.items.map((item: Item) => (
          <WishlistItem
            key={item.id}
            item={item}
            getProductData={getProductData}
            getRefinedProduct={getRefinedProduct}
            moveProdToCart={moveProdToCart}
            routeProdDetailPage={routeProdDetailPage}
            imageNode={
              slots?.image
                ? ({ defaultImageProps }) => (
                    <Slot
                      name="image"
                      slot={slots.image}
                      context={{
                        defaultImageProps,
                        item,
                      }}
                    />
                  )
                : undefined
            }
          />
        ))
      : null;
  }, [wishlistData, moveProdToCart, getProductData, getRefinedProduct, routeProdDetailPage, slots?.image]);

  useEffect(() => {
    if (wishlistAlert) {
      setAlert(wishlistAlert);

      const timer = setTimeout(() => {
        setAlert(null);
      }, 5000);

      return () => clearTimeout(timer);
    }
  }, [wishlistAlert]);

  const renderAlert = useMemo(
    () =>
      alert ? (
        <VComponent node={alert} className="wishlist-wishlist__alert" />
      ) : null,
    [alert]
  );

  const renderLoader = () => {
    return (
      <>
        <div
          className="wishlist-wishlist__heading"
          data-testid="wishlist-heading-wrapper"
        >
          <h1
            className="wishlist-wishlist__heading-text"
            data-testid="loader-wishlist-heading"
          >
            {dictionary.wishlistLoadingHeading}
          </h1>
        </div>
        <WishlistSkeleton />
      </>
    );
  };

  const renderWishlist = () => {
    return products ? (
      <>
        {renderHeading}
        <div className="wishlist-wishlist__content">{products}</div>
      </>
    ) : (
      <div
        className={classes([
          'wishlist-wishlist__content',
          'wishlist-wishlist__content--empty',
        ])}
      >
        <div>
          <EmptyWishlist
            data-testid="empty-wishlist"
            ctaLinkURL={routeEmptyWishlistCTA?.()}
          />
          {!isLoggedIn && <Login onLoginClick={onLoginClick} />}
        </div>
      </div>
    );
  };

  const renderHeading = useMemo(() => {
    if (!products) return null;

    return (
      <div
        className="wishlist-wishlist__heading"
        data-testid="wishlist-heading-wrapper"
      >
        <h1
          className="wishlist-wishlist__heading-text"
          data-testid="default-wishlist-heading"
        >
          {dictionary.wishlistHeading
            ?.split(' {count}')
            .map((title: string, index: number) => (
              <Fragment key={wishlistData?.id?.toString() + index}>
                {title}
                {index === 0 && (
                  <span
                    className="wishlist-wishlist__heading-count"
                    data-testid="wishlist-heading-count"
                  >
                    {`${wishlistData?.items_count} products`}
                  </span>
                )}
              </Fragment>
            ))}
        </h1>
      </div>
    );
  }, [dictionary, products, wishlistData]);

  return (
    <div {...props} className={classes(['wishlist-wishlist', className])}>
      {renderAlert}
      {isLoading ? renderLoader() : renderWishlist()}
    </div>
  );
};
